import yaml from "js-yaml"
import {showing, characters} from "./controls.js"
import {v4 as uuid} from "uuid"

async function ingestYAML (path) {
  const resp = await fetch(path)
  const text = await resp.text()
  return yaml.load(text)
}

async function injestHTML (path) {
  const resp = await fetch(path)
  return await resp.text()
}

async function system (name) {
  const [
    combat,
    characterBuilding,
    archetypes,
    equipment,
    chassis,
    prototypes,
    adversaries
  ] = await Promise.all([
    injestHTML(`./${name}/combat.html`),
    injestHTML(`./${name}/character.html`),
    ingestYAML(`./${name}/archetypes.yaml`),
    ingestYAML(`./${name}/equipment.yaml`),
    ingestYAML(`./${name}/chassis.yaml`),
    ingestYAML(`./${name}/prototypes.yaml`),
    ingestYAML(`./${name}/adversaries.yaml`)
  ])

  characters.next(JSON.parse(localStorage.getItem(`${name}-characters`) || "{}"))

  return {
    archetypes,
    equipment,
    chassis,
    combat,
    characterBuilding,
    prototypes,
    characters,
    adversaries,
    apply: (selections) => {
      const character = {
        name: selections.name,
        blurb: selections.blurb,
        level: selections.level,
        archetypes: selections.archetypes.map(arch => archetypes[arch]),
        equipment: selections.equipment.map(equip => equipment[equip]),
        stats: {
          Evasion: selections.stats.Evasion,
          Defense: selections.stats.Defense,
          Resilience: selections.stats.Resilience,
          Speed: selections.stats.Speed,
          Energy: selections.stats.Energy,
          Focus: selections.stats.Focus
        },
        powers: [],
        selections
      }

      Object.values(character.archetypes).forEach(archetype => {
        Object.values(archetype.basePowers).forEach(power => {
          character.powers.push(power)
        })
        selections.upgrades.forEach(upgrade => {
          if (Object.keys(archetype.upgrades).includes(upgrade)) {
            character.powers.push(archetype.upgrades[upgrade])
          }
        })
      })

      return character
    },
    save: (selections) => {
      if (!selections.id) {
        selections.id = uuid()
      }
      const chars = characters.getValue()
      chars[selections.id] = selections
      localStorage.setItem(`${name}-characters`, JSON.stringify(chars))
      characters.next(chars)
      showing.next(["characters", selections])
    }
  }
}

export default system
