<script>
export let doc

function parse_apply(effects) {
  if (!effects) return ""
  let desc = "<table>"
  effects.forEach(effect => {
    desc+=`<tr><td class="table-header">${effect.target}</td><td class="table-item">${effect.apply[0]}</td></tr>`
    effect.apply.slice(1).forEach(app => {
      desc+=`<tr><td/><td>${app}</td></tr>`
    })
  })
  desc += "</table>"
  return desc
}


$: desc = doc.description ? doc.description.trim().replace("\n", "<br><br>") : parse_apply(doc.effects)
</script>

<div class="action col-flex" style="justify-content: space-between;">
  <div class="vertical-section" style="display: grid; grid-template-columns: 3fr 2fr;">
    <div style="grid-column: 1; grid-row: 1;">
      <p class="action-name"><i><b>{doc.name}</b></i></p>
    </div>
    <div style="grid-column: 2; grid-row: 1;">
      <p style="text-align: end;"><i>{doc.action}</i></p>
      {#if doc.repeatable}
        <p style="text-align: end;">Repeatable</p>
      {/if}
      {#if doc.uses}
        <p style="text-align: end;">{doc.uses}</p>
      {/if}
      {#if doc.concentration}
        <p style="text-align: end;">({doc.concentration})</p>
      {/if}
    </div>
  </div>
  <div class="vertical-section">
    <p>{@html desc}</p>
  </div>
  {#if doc.flavor}
  <div class="vertical-section">
    <p><i>{doc.flavor}</i></p>
  </div>
  {/if}
</div>
