<script>
import Power from "./Power.svelte"


export let selections
export let system

const classUpgrades = {}

$: classUpgrades[selections.class] = selections.upgrades
$: character = system ? system.apply(selections) : null
$: attack = character.powers.filter(act => act.action == "Attack")
$: tactics = character.powers.filter(act => act.action == "Tactics")
$: guard = character.powers.filter(act => act.action == "Guard")
$: move = character.powers.filter(act => act.action == "Move")
$: passives = character.powers.filter(act => act.action == "Passive")
$: statPoints = 30+character.level*2-Object.values(character.stats).reduce((a, b) => a+b, 0)

</script>

<article>
{#if character}
  <h2>{character.name}</h2>
  <p><i>{character.blurb}</i></p>
  <p style="padding-bottom: 1rem;"><b>{character.archetypes[0].name} - {character.archetypes[1].name} {character.level}</b></p>

  <div class="powers">
    <div class="action col-flex" style="justify-content: space-between;">
      <table class="statblock">
        <caption>Stats</caption>
        <tbody>
          {#each Object.entries(character.stats) as [stat, value]}
            <tr><td class="stat">{stat}</td><td>{value}<td></tr>
          {/each}
        </tbody>
      </table>
    </div>

    {#if passives.length}
      {#each passives as passive}
        <Power bind:doc={passive}/>
      {/each}
    {/if}

    {#if attack.length}
      {#each attack as action}
        <Power bind:doc={action}/>
      {/each}
    {/if}

    {#if tactics.length}
      {#each tactics as action}
        <Power bind:doc={action}/>
      {/each}
    {/if}

    {#if guard.length}
      {#each guard as action}
        <Power bind:doc={action}/>
      {/each}
    {/if}

    {#if move.length}
      {#each move as action}
        <Power bind:doc={action}/>
      {/each}
    {/if}
  </div>
{/if}
</article>

<div class="context">
  <button on:click={() => system.save(selections)}>Save</button>

  <h5 class="selections" for="character-name">Name</h5>
  <input id="character-name" bind:value={selections.name}/>

  <h5 class="selections" for="character-blurb">Blurb</h5>
  <textarea id="character-blurb" bind:value={selections.blurb}/>

  <h5 class="selections" for="character-level">Level</h5>
  <input id="character-level" type=number min="1" max="10" bind:value={selections.level}>

  <h5 class="selections" for="character-affinities">Stats ({statPoints})</h5>
  <table id="character-stats">
    {#each Object.entries(selections.stats) as [key, val]}
      <tr>
        <td><label for="character-{key}">{key}</label></td>
        <td><input id="character-{key}" type=number min="1" max="10" bind:value={selections.stats[key]}></td>
      </tr>
    {/each}
  </table>

  <h5 class="selections" for="character-archetypes">Archetypes</h5>
  {#each selections.archetypes as archetype, i}
    <select id="character-archetypes-{i}" bind:value={selections.archetypes[i]}>
      {#each Object.entries(system.archetypes) as [key, arch]}
        <option value={key}>{arch.name}</option>
      {/each}
    </select>
    {#each Object.entries(system.archetypes[archetype].upgrades) as [key, cls]}
      <label>
        <input type="checkbox" bind:group={selections.upgrades} name="upgrades" value={key}>
        {cls.name}
      </label>
    {/each}
  {/each}

</div>

<style>

</style>
