<script>
import { beforeUpdate } from "svelte";
import Toolbar from "./Toolbar.svelte"
import Rules from "./Rules.svelte"
import Archetype from "./Archetype.svelte"
import Character from "./Character.svelte"
import Monster from "./Monster.svelte"
import Equipment from "./Equipment.svelte"
import Chassis from "./Chassis.svelte"
import { showing } from "./controls.js"
import system from "./system.js"

let systemName = "goliath"


function setupSystem(name) {
	async function fetchData () {
		return await system(name)
	}
	return fetchData
}


</script>

{#await system(systemName)}
<header class="dragonsteel">
	<h1 class="dragonsteel">{systemName}</h1>
	<small class="dragonsteel">built with ersatz</small><br>
	<small class="dragonsteel">&copy; Mark Dewey and Stuart Cheney</small>
</header>

{:then system}
<header class="dragonsteel">
	<h1 class="dragonsteel">{systemName}</h1>
	<small class="dragonsteel">built with ersatz</small><br>
	<small class="dragonsteel">&copy; Mark Dewey and Stuart Cheney</small>
</header>

<Toolbar system={system} bind:systemName={systemName}/>

<main>
	{#if $showing[0] === "rules"}
	  <Rules doc={$showing[1]} />
	{/if}
	{#if $showing[0] === "archetypes"}
		<Archetype doc={$showing[1]} />
	{/if}
	{#if $showing[0] === "chassis"}
		<Chassis doc={$showing[1]} />
	{/if}
	{#if $showing[0] === "equipment"}
		<Equipment doc={$showing[1]} />
	{/if}
	{#if $showing[0] === "prototypes"}
		<Character selections={JSON.parse(JSON.stringify($showing[1]))} system={system} />
	{/if}
	{#if $showing[0] === "characters"}
		<Character selections={JSON.parse(JSON.stringify($showing[1]))} system={system} />
	{/if}
	{#if $showing[0] === "adversaries"}
		<Monster doc={$showing[1]} />
	{/if}
</main>
{:catch error}
	<h1>500</h1>
{/await}

