<script>
import {showing, characters} from "./controls.js"

export let system;
export let systemName;

$: choices = {
  rules: {
    name: "Rules",
    children: {
      combat: {name: "Combat", details: system.combat},
      character: {name: "Building a Character", details: system.characterBuilding}
    },
    open: true,
  },
  archetypes: {
    name: "Archetypes",
    children: system.archetypes,
    open: true
  },
  chassis: {
    name: "Chassis",
    children: system.chassis,
    open: true
  },
  equipment: {
    name: "Equipment",
    children: system.equipment,
    open: true
  },
  prototypes: {
    name: "Prototypes",
    children: system.prototypes,
    open: true
  },
  characters: {
    name: "Characters (cached)",
    children: $characters,
    open: true
  },
  adversaries: {
    name: "Adversaries",
    children: system.adversaries,
    open: true
  }
}

let selection = {
  heading: "",
  option: ""
}
showing.subscribe(([heading, option]) => {
  selection = {
    heading,
    option
  }
})
</script>


<aside>
  <select bind:value={systemName}>
		<option value="dragonsteel">Dragonsteel</option>
		<option value="goliath">Goliath</option>
	</select>
  {#each Object.keys(choices) as heading}
  <p on:click={() => choices[heading].open = !choices[heading].open} class="openable"><b>{choices[heading].name}<b></p>
    {#if choices[heading].open}
      {#each Object.keys(choices[heading].children) as option}
      	<a href="#top" on:click={() => showing.next([heading, choices[heading].children[option]])}
           class="navigation {selection.heading === heading && selection.option.name === choices[heading].children[option].name ? 'active' : ''}">
      		<span class="navigation">{choices[heading].children[option].name}</span>
      	</a>
      {/each}
    {/if}
  {/each}
</aside>
