<script>
export let doc

function powerText(power) {
  const qualities = []
  if (power.action != "Passive") qualities.push(power.action)
  if (power.uses && power.uses != "At-Will") qualities.push(power.uses)
  if (power.description) qualities.push(power.description)

  if (power.effects) qualities.push(power.effects.map(effect => {
    return `${effect.target}: ${effect.apply.join(", ")}`
  }).join("; "))
  return qualities.join(" - ") + "."
}
</script>

<div class="monster big-card">
  <h6 class="monster-name">{doc.name} ({doc.class} {doc.level})</h6>
  <p class="monster-flavor"><i>{doc.flavor}<i></p>
  <table class="monster-stats statblock">
    <tbody>
      {#each Object.entries(doc.stats) as [stat, value]}
        <tr><td class="stat">{stat}</td><td>{value}<td></tr>
      {/each}
    </tbody>
  </table>
  <div class="monster-powers">
    {#each Object.values(doc.powers) as power}
    <p>
      <i><b>{power.name}</b></i> - {powerText(power)}
    </p>
    {/each}
  </div>
</div>

<style>
.monster {
  display: grid;
  grid-template-columns: 8rem 2fr;
  grid-template-rows: 2rem 1fr min-content;
  grid-template-areas:
    "name name"
    "flavor powers"
    "stats powers";
  gap: 1rem;
}

.monster-name {
  grid-area: name;
}

.monster-flavor {
  grid-area: flavor;
}

.monster-powers {
  grid-area: powers
}
</style>
